<template>
  <div class="container" v-if="weather">
    <Navbar/>
    <Main/>
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue';
import Main from './components/Main.vue';
import { mapState, mapActions } from 'vuex'

  export default {
    components: { 
      Navbar,
      Main
    },
    created() {
      setTimeout(() => {
        this.getWeather('Нью-Йорк')
      }, 1000);
    },
    computed: {
      ...mapState(['weather'])
    },
    methods: {
      ...mapActions(['getWeather'])
    },
}
</script>
